<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <div class="addCon">
        <b-button variant="warning" @click="addSeqPlatForm">+ 测序平台</b-button>
        <b-modal ref="addSeqPlatFormModal" hide-footer no-close-on-backdrop title="测序平台 -- 添加">
          <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event)">
            <b-form-group id="add_seq_platform_nameGroup" label="测序平台名称:" label-size="sm" label-for="add_seq_platform_name">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_seq_platform_name" v-model.trim="addForm.seq_platform_name" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_seq_platform_abbrGroup" label="测序平台简称:" label-size="sm" label-for="add_seq_platform_abbr">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_seq_platform_abbr" v-model.trim="addForm.seq_platform_abbr" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_seq_platform_descGroup" label="测序平台描述:" label-size="sm" label-for="add_seq_platform_desc">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_seq_platform_desc" v-model.trim="addForm.seq_platform_desc" required>
              </b-form-input>
            </b-form-group>
            <b-button type="submit" style="width:100%" variant="info">添加</b-button>
          </b-form>
        </b-modal>
      </div>
      <b-table bordered responsive hover :items="seqPlatFormLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="edit(row)">
           编辑
          </b-button>
          <b-button size="sm" class="mr-2" variant="danger" @click="openDeleteModal(row)">
           删除
          </b-button>
          <b-modal :ref="'deleteSeqPlatFormModal'+row.index" title="测序平台 -- 删除" cancel-title="取消" ok-title="确定" @ok="deleteSeqPlatForm(row)">
            <p class="my-4" style="text-align:left">是否确定要删除 测序平台名称：<b>{{row.item.seq_platform_name}}</b> ？</p>
          </b-modal>
          <b-modal :ref="'editSeqPlatFormModal'+row.index" hide-footer no-close-on-backdrop title="测序平台 -- 编辑">
            <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event,row)">
              <b-form-group :id="'edit_seq_platform_nameGroup'+row.index" label="测序平台名称:" label-size="sm" :label-for="'edit_seq_platform_name'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_seq_platform_name'+row.index" v-model.trim="currentForm.seq_platform_name" required disabled>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_seq_platform_abbrGroup'+row.index" label="测序平台简称:" label-size="sm" :label-for="'edit_seq_platform_abbr'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_seq_platform_abbr'+row.index" v-model.trim="currentForm.seq_platform_abbr" required>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_seq_platform_descGroup'+row.index" label="测序平台描述:" label-size="sm" :label-for="'edit_seq_platform_desc'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_seq_platform_desc'+row.index" v-model.trim="currentForm.seq_platform_desc" required>
                </b-form-input>
              </b-form-group>
              <b-button type="submit" style="width:100%" variant="info">提交</b-button>
            </b-form>
          </b-modal>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>
  export default{
    mounted(){
      
      this.getTableData()
      
    },
    data () {
      return {
        menus: [{
          text: '测序平台管理',
          href: '#'
        }],
        addForm:{
          seq_platform_name:'',
          seq_platform_abbr:'',
          seq_platform_desc:''
        },
        currentForm:{
          seq_platform_name:'',
          seq_platform_abbr:'',
          seq_platform_desc:''
        },
        fields: {
          seq_platform_name:{
            label:'测序平台名称'
          },
          seq_platform_abbr:{
            label:'测序平台简称'
          },
          seq_platform_desc:{
            label:'测序平台描述'
          },
          operate:{
            label:'操作',
            thStyle:{
              width:'150px'
            }
          }
        },
        tableEmptyText:'正在请求数据...',
        seqPlatFormLists: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/Seqplatform/listseqplatform",qs.stringify({page:this.currentPage})).then(response=>{
          // console.log(response.data)
          this.seqPlatFormLists=response.data.data
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      addSeqPlatForm(){
        this.$refs.addSeqPlatFormModal.show()
      },
      edit(row){
        if(this.currentForm.seq_platformid!==row.item.seq_platformid){
          for(var k in row.item){
            this.currentForm[k]=row.item[k]
          }
        }
        this.$refs['editSeqPlatFormModal'+row.index].show()
      },
      openDeleteModal(row){
        this.$refs['deleteSeqPlatFormModal'+row.index].show()
      },
      deleteSeqPlatForm(row){
        this.$http.post("/api/Seqplatform/closeseqplatform",qs.stringify({seq_platformid:row.item.seq_platformid})).then(response=>{
          this.getTableData()
          this.$Loading.finish()
        })
      },
      onSubmit(evt,row){
        // this.$Loading.start()
        // 编辑和添加时同意接口，将根据传的全称判断是否存在，不存在则添加，存在则视为编辑，若已关闭，会再次打开 
        evt.preventDefault();
        if(row){
          this.$http.post("/api/Seqplatform/addseqplatform",qs.stringify(this.currentForm)).then(response=>{
            this.getTableData()
            this.currentForm={
              seq_platform_name:'',
              seq_platform_abbr:'',
              seq_platform_desc:''
            }
            this.$refs['editSeqPlatFormModal'+row.index].hide()
            this.$Loading.finish()
          })
        }else{
          // 添加
          // console.log(this.addForm)
          this.$http.post("/api/Seqplatform/addseqplatform",qs.stringify(this.addForm)).then(response=>{
            this.getTableData()
            this.addForm={
              seq_platform_name:'',
              seq_platform_abbr:'',
              seq_platform_desc:''
            }
            this.$refs.addSeqPlatFormModal.hide()
            this.$Loading.finish()
          })
        }
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../less/init.less';
.btn-sm{
  padding: .08rem .5rem !important;
}
.addCon{
  text-align: right;
  padding-right: 20px;
  margin-bottom:10px;
}
.paginationCon{
  overflow: hidden;
  .pagination{
    float: right;
  }
}

</style>


